@import '_variables';
@import '_mixin';

/**
 * @type		component
 * @name		info-popup
 * @moudle		module-a
 * @update		2022-09-16
 */

 html.laptop.active-info-popup,
 html.mobile.active-info-popup,
 html.active-info-popup {
	 body {
		 overflow-y: hidden;
	 }
 }
 
 .info-popup.module-a {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex-direction: column;
	 position: fixed;
	 z-index: 60;
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 padding: 24rem;
	 visibility: hidden;
	 opacity: 0;
	 background-color: rgba(0,0,0,0.4);
	 pointer-events: none;
	 transition: visibility 0.25s ease, opacity 0.25s ease;
 
	 &.active {
		 visibility: visible;
		 opacity: 1;
		 pointer-events: auto;
	 }
	 
	 .popup-page-body {
		 pointer-events: auto;
		 display: flex;
		 position: relative;
		 overflow: hidden;
		 width: 480rem;
		 max-width: 100%;
		 background-color: color(base, 000);
		 box-shadow: 0 8rem 16rem 0 rgb(0 0 0 / 10%), 0 0 0 0.0625rem rgb(0 0 0 / 15%);
		 
		 .popup-local {
			 display: flex;
			 flex-direction: column;
			 overflow: hidden;
			 width: 100%;
			 
			 .popup-local-head {
				padding: 16rem 32rem;
				background-color: color(normal, 000);
				border-bottom: 1rem solid color(base, 010);

				.popup-local-subject {
					margin: 0;
					
					svg {
						display: block;
						width: 24rem;
						height: 24rem;
						fill: color(accent, 010);
					}
				}
			 }
 
			 .popup-local-body {
				 flex: 1 1 100%;
				 padding: 0 32rem;
				 @include overflow-scroll(default, dark, medium, y);
				 
				 .popup-content {
					 .popup-content-body {
						 margin: 32rem 0;

						 @include respond-above(large-1x) {
							margin: 40rem 0;
						}
				 
						 >.button-display {			
							margin: 32rem 0;

							@include respond-above(large-1x) {
								margin: 40rem 0;
							}

							 .btn {
								 flex: 1 1 100%;
							 }
						 }
					 }
				 }
			 }
 
			 .popup-local-func {
				 position: absolute;
				 top: 16rem;
				 right: 24rem;
 
				 .button-area {
					 display: block;
 
					 .btn.popup-close {
						display: block;
						 svg {
							display: block;
							 width: 24rem;
							 height: 24rem;
							 fill: color(normal, 100);
						 }
					 }
				 }
			 }		
		 }
	 }
 }
 
 /**
  * @type		component
  * @name		content-popup
  * @moudle		module-a
  * @update		2022-09-16
  */
  html.laptop.active-content-popup,
  html.mobile.active-content-popup,
  html.active-content-popup {	
	 body {
		 overflow-y: hidden;
	 }
 }
 
 .content-popup.module-a {
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 flex-direction: column;
	 position: fixed;
	 z-index: 60;
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 visibility: hidden;
	 opacity: 0;
	 pointer-events: none;
	 transition: visibility 0.25s ease, opacity 0.25s ease;
 
	 &.active {
		 visibility: visible;
		 opacity: 1;
	 }
	 
	 &.medium {
		 // laptop
		 @include respond-above(large-1x) {
			 .popup-page-body {
				 width: 704rem;
			 }
		 }
	 }
 
	 &.large {
		 // laptop
		 @include respond-above(large-1x) {
			 .popup-page-body {
				 width: 1000rem;
			 }
		 }
	 }
 
	 .popup-page-body {
		 pointer-events: auto;
		 display: flex;
		 position: relative;
		 overflow: hidden;
		 max-width: 100%;
		 box-sizing: border-box;
		 background-color: color(base, 000);
		 box-shadow: 0 8rem 16rem 0 rgb(0 0 0 / 10%), 0 0 0 0.0625rem rgb(0 0 0 / 15%);
 
		 // mobile
		 @include respond-below(large-1x) {
			 height: 100vh;
			 width: 100vw;
		 }
		 
		 .popup-local {
			 display: flex;
			 flex-direction: column;
			 overflow: hidden;
			 width: 100%;
			 
			 .popup-local-head {
				 padding: 16rem 24rem;
				 background-color: color(normal, 000);
				 border-bottom: 1rem solid color(base, 020);
 
				 // laptop
				 @include respond-above(large-1x) {
					 padding: 24rem 32rem;
				 }
 
				 .popup-local-subject {
					 margin: 0;
					 text-align: center;
					 @include font((outline: body, size: small, weight: medium)...);
 
					 // laptop
					 @include respond-above(large-1x) {
						 @include font((outline: body, size: large-1x)...);
					 }
 
					 .popup-local-name {
						 display: block;
					 }
				 }
			 }
 
			 .popup-local-body {
				 flex: 1 1 100%;
				 @include overflow-scroll(default, dark, medium, y);
 
				 padding: 0 20rem;
 
				 // laptop
				 @include respond-above(large-1x) {
					 padding: 0 48rem;
				 }
				 
				 .popup-content {
					 .popup-content-body {
						 margin-bottom: 40rem;
 
						 .section.privacy {
							 .section-wrap {
								 .section-head {
									 .section-subject {
										 @include font ((outline: body, size: small, weight: bold)...);
										 color: color(base, 090);
										 margin: 40rem 0 16rem 0 ;
										 padding: 0;
 
										 @include respond-above(large-1x) {
											 @include font ((outline: body, size: medium, weight: bold)...);
											 margin: 40rem 0 20rem 0;
										 }
									 }
								 }
								 .section-body {
									 .data-display.module-b {
										 .data-list {
											 .data-item {
												 .data-wrap {
													 flex: 1 1 100%;
													 display: flex;
													 // align-items: stretch;
													 
													 .data-head {
														 flex: 1 0 96rem;
														 display: flex;
														 align-items: center;
														 justify-content: center;
														 width: 96rem;
														 padding: 12rem 14rem;
														 text-align: center;
														 background-color: color(base, 005);
														 box-sizing: border-box;
														 
														 .data-subject {
															 display: block;
															 @include font((outline: etc, size: small-1x, weight: bold)...);
															 color: color(base, 090);
									 
															 .data-name {
																 display: block;
															 }	
														 }
													 }
									 
													 .data-body {
														 flex: 1 1 100%;
														 display: block;
														 padding: 10rem 12rem;
														 @include font((outline: etc, size: small-1x)...);
														 color: color(base, 060);
									 
														 a.em {
															 text-decoration: none;
									 
															 &:hover {
																 text-decoration: underline;
																 color: color(accent, 010);
															 }
														 }
									 
													 }
												 }
											 }
										 }
										 // laptop
										 @include respond-above(large-1x) {
											 .data-list {
												 display: flex;
												 flex-wrap: wrap;
												 position: relative;
									 
												 &::after {
													 content: '';
													 display: block;
													 position: absolute;
													 bottom: 0;
													 left: 0;
													 width: 100%;
													 height: 1rem;
													 background-color: color(base, 020);
												 }
									 
												 .data-item {
													 width: 50%;
									 
													 .data-wrap {
														 display: flex;
														 height: 100%;
														 
														 .data-head {
															 flex: 1 0 116rem;
															 width: 116rem;
															 padding: 16rem 12 rem;
									 
															 .data-subject {
																 @include font((outline: body, size: small-1x, weight: bold)...);
															 }
														 }
														 .data-body {
															 padding: 16rem 16rem;
															 @include font((outline: body, size: small-1x)...);
														 }
													 }
												 }
											 }
										 }
									 
										 &.style-b {
											 .data-list {
												 border-top: 2rem solid color(normal, 100);
									 
												 .data-item {
													 border-bottom: 1rem solid color(base, 020);
												 }
											 }
										 }
										 
									 }
								 }
							 }
						 }
					 }
				 }
			 }
 
			 .popup-local-func {
				 position: absolute;
				 top: 16rem;
				 right: 16rem;
 
				 // laptop
				 @include respond-above(large-1x) {
					 top: 20rem;
					 right: 20rem;
				 }
 
				 .button-area {
					 display: block;
					 
					 .btn.popup-close {
						 display: block;
 
						 svg {
							 width: 32rem;
							 height: 32rem;
							 fill: color(normal, 010);
 
							 // laptop
							 @include respond-above(large-1x) {
								 width: 40rem;
								 height: 40rem;
							 }
						 }
					 }
				 }
			 }		
		 }
	 }
 }
 

#rejectEmail {
	.popup-content-body {
		.data-display {
			.data-list {
				.data-item {
					.data-wrap {
						.data-head {
							.data-subject {
								margin: 0;
								@include font((outline: head, size: small-1x, weight: semi-bold)...);
								color: color(normal, 100);
								
								.data-name {
									display: block;
								}
							}
						}
						.data-body {
							margin-top: 32rem;
							@include font((outline: body, size: small-1x, weight: extra-light)...);
							color: color(normal, 010);
						}
						.data-date {
							margin: 16rem 0 0 0;
							@include font((outline: body, size: small-1x, weight: extra-light)...);
							color: color(normal, 030);
						}
					}
				}
			}
		}
	}
}

