#privacy {
	.popup-content-body {
		>.data-display.privacy {
			>.data-list {
				>.data-item {
					>.data-wrap {
						>.data-head {
							>.data-subject {
								margin: 0;
								@include font((outline: head, size: small-1x, weight: semi-bold)...);
								color: color(normal, 100);
								
								.data-name {
									display: block;
								}
							}
						}
						>.data-body {
							margin-top: 32rem;
							>.data-display {
								>.order-list {
									>.order-item {

										+.order-item {
											margin-top: 16rem;
										}

										>.data-wrap {
											display: flex;
											flex-direction: column;
											row-gap: 8rem;

											>.data-head {
												>.data-subject {
													margin: 0;
													@include font((outline: body, size: small-1x, weight: semi-bold)...);
													color: color(normal, 010);
													.data-name {
														display: block;
													}
												}
											}
											>.data-body {
												padding-left: 2rem;

												>.data-list {
													list-style: upper-alpha;

													.data-item {
														&::marker {
															@include font((weight: extra-light)...);
															color: color(normal, 010);
														}

														.data-wrap {
															.data-head {
																.data-name {
																	display: block;
																	@include font((outline: body, size: small-1x, weight: extra-light)...);
																	color: color(normal, 010);
																}
															}
															.data-body {
																display: flex;
																flex-direction: column;
																@include font((outline: body, size: small-1x, weight: extra-light)...);
																color: color(normal, 010);
															}
														}
													}
												}
											}
										}
									}
								}
							}
						}
						.data-date {
							margin: 16rem 0 0 0;
							@include font((outline: body, size: small-1x, weight: extra-light)...);
							color: color(normal, 030);
						}
					}
				}
			}
		}
	}
