@import 'partials/_variables';
@import 'partials/_mixin';
@import 'partials/_popup';

// mobile
@include respond-below(large-1x) {
}

// laptop
@include respond-above(large-1x) {
}

:root {
	height: 100%;
	font-size: 1px;
	// scroll-behavior: smooth;
}

body {
	margin: 0;
	padding: 0;
	height: 100%;
	word-break: keep-all;
	@include font((outline: body, size: small-1x, family: default)...);
	-webkit-text-size-adjust: none;
	@include overflow-scroll(default, dark, large, y);
}

hr {
	display: none;
}

img {
	max-width: 100%;
}

/**
 * @type layout
 * @name global
 */
#wrap {
	position: relative;
	overflow: hidden;
	padding-right: 20rem;
	padding-left: 20rem;

	// laptop
	@include respond-above(large-1x) {
		padding-right: 80rem;
		padding-left: 80rem;
	}

	#page {
		margin: 0 auto;
		width: 1280rem;
		// padding-top: 56rem;
		max-width: 100%;
		box-sizing: border-box;

	}
}

/**
 * @type layout
 * @name page-skip
**/
.page-skip {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	z-index: 100;
	top: -120rem;
	right: 0;
	left: 0;
	height: 60rem;
	background-color: color(base, 090);

	// laptop
	@include respond-above(large-1x) {
		height: 80rem;
	}

	.skip-item {
		color: color(base, 000);
		@include font((outline: body, size: small-1x)...);
	}
	
	&:focus-within {
		top: 0;
	}
}

/**
 * @type layout
 * @name page-head
**/
.page-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	position: fixed;
	z-index: 20;
	top: 0;
	right: 0;
	left: 0;
	background-color: transparent;
	
	&::before {
		content: '';
		display: block;
		position: absolute;
		z-index: -1;
		top: 0;
		bottom: 0;
		left: calc(50% - 50vw);
		width: 100vw;
		background-color: color(normal, 000);
		transition: background-color 0.25s ease;
		box-shadow: 0 1rem 0 0 rgba(0,0,0,0.1);
	}

	// mobile
	@include respond-below(large-1x) {
		display: flex;
		align-items: center;
		overflow: hidden;
		position: fixed;
		z-index: 50;
		top: 0;
		right: 0;
		left: 0;
		padding: 0 16rem 0 20rem;
		box-shadow: 0 1rem 0 0 rgba(0,0,0,0.1);

		.page-subject {
			flex-shrink: 0;
			height: 56rem;
			margin-right: auto;
			box-sizing: border-box;

			svg {
				display: block;
				width: 28rem;
				height: 28rem;
			}
		}

		.page-control {
			order: 4;
			display: block;
			align-items: center;

			.button-area {
				display: flex;
				column-gap: 8rem;
				align-items: center;
				
				.btn {
					display: block;

					svg {
						display: block;
						width: 28rem;
						height: 28rem;
				
						line {
							transition-property: transform;
							transition-duration: 0.25s;
							transform-origin: center;
							stroke: color(normal, 010);
							stroke-width: 6%;
						}
				
						.ob-01 {
							transform: translateY(-20%);
						}
							
						.ob-03 {
							transform: translateY(20%);
						}
					}

					&.active {
						.ob-01 {
							transform: rotate(45deg);
						}
						
						.ob-02 {
							transform: scaleX(0.1);
						}
						
						.ob-03 {
							transform: rotate(-45deg);
						}
					}
				}
			}
		}
	}

	// laptop
	@include respond-above(large-1x) {
		margin: 0 auto;
		padding: 0 80rem;
		max-width: calc(100% - 80rem);
		height: 80rem;
	}

	.page-subject {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0;

		// laptop
		@include respond-above(large-1x) {
			position: absolute;
		} 

		.page-name {
			display: block;

			svg {
				display: block;
				width: 28rem;
				height: 28rem;
				fill: color(accent, 010);
				transition: fill 0.25s ease;
			}
		}
	}

	.page-control {
		order: 4;
		display: block;
		align-items: center;

		.button-area {
			display: flex;
			column-gap: 8rem;
			align-items: center;
			
			.btn {
				display: block;

				svg {
					width: 28rem;
					height: 28rem;
			
					line {
						transition-property: transform;
						transition-duration: 0.25s;
						transform-origin: center;
						stroke: color(normal, 100);
						stroke-width: 5%;
					}
			
					.ob-01 {
						transform: translateY(-20%);
					}
						
					.ob-03 {
						transform: translateY(20%);
					}
				}

				&.active {
					.ob-01 {
						transform: rotate(45deg);
					}
					
					.ob-02 {
						transform: scaleX(0.1);
					}
					
					.ob-03 {
						transform: rotate(-45deg);
					}
				}
			}
		}

		// laptop
		@include respond-above(large-1x) {
			display: none;
		}
	}
}

.page-navi {
	.section-wrap {
		.section-head {
			.section-subject {
				@include hidden;
			}
		}

		.section-body {
			.lnb-list,
			.gnb-list {
				margin: 0;
				padding: 0;
				list-style: none;
			}
		}
		.section-func {
			position: absolute;
			top: 16rem;
			right: 12rem;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.25s ease, visibility 0.25s ease;
	
			.button-area {
				display: flex;
	
				.btn.close {
					display: block;
	
					svg {
						width: 24rem;
						height: 24rem;
					}
				}
			}
		}

		.section-util {
			.btn.ir-download,
			.btn.inquiry {
				display: flex;
				align-items: center;
				justify-content: center;
				border: 1rem solid color(accent, 010);
				
				box-sizing: border-box;
				@include font((outline: body, size: small-2x, weight: semi-bold)...);
				color: color(accent, 010);

				&:not(:hover) {
					text-decoration: none;
				}
			}
		}
	}

	// mobile
	@include respond-below(large-1x) {
		order: 2;
		display: flex;
		justify-content: flex-end;
		position: fixed;
		top: 56rem;
		right: 0;
		bottom: 0;
		left: 0;
		overflow: hidden;
		visibility: hidden;
		pointer-events: none;
		background-color: rgba(0,0,0,0);
		transition: background-color 0.25s ease, visibility 0.25s ease;

		&.active {
			pointer-events: auto;
			visibility: visible;
			background-color: rgba(0,0,0,0.4);
			
			.section-wrap {
				transform: translate(-100%, 0);
			}
		}

		@at-root {
			html {
				&.active-page-navi {
					body {
						height: 100%;
						overflow: hidden;
					}
				}

				&.laptop {
					.page-navi {
						.section-wrap {
							@include overflow-scroll(default, dark);
						}
					}
				}
			}
		}
		
		.section-head {
			.section-subject {
				@include hidden;
			}
		}

		.section-wrap {
			display: flex;
			flex-direction: column;
			position: relative;
			left: 100%;
			padding: 56rem 20rem;
			width: 100%;
			min-height: 100%;
			box-sizing: border-box;
			box-shadow: inset 0 1rem 0 0 rgba(0,0,0,0.1);
			transform: translate(0, 0);
			transition: transform 0.25s ease-in-out;
			background-color: color(normal, 000);
			@include overflow-scroll(default, dark, medium, y);

			.section-body {
				flex: 1 1 100%;

				.gnb-list {
					display: flex;
					flex-direction: column;
					row-gap: 16rem;
					margin: 0;
					padding: 0;
					list-style: none;
					overflow: hidden;
					
					.gnb-item {
						position: relative;
						display: flex;
						row-gap: 4rem;
						flex-direction: column;
						align-items: flex-start;

						.gnb-name {
							display: block;
							position: relative;
							padding: 16rem 0;
							@include font((outline: head, size: small-1x, weight: semi-bold)...);
							color: color(normal, 010);
							text-decoration: none;
						}


						.lnb-list {
							display: flex;
							flex-direction: column;
							row-gap: 10rem;
							position: relative;
							left: calc(50% - 50vw);
							margin: 0;
							padding: 0 20rem;
							list-style: none;
							width: 100vw;
							box-sizing: border-box;
							// background-color: red;

							.lnb-item {
								display: flex;
								row-gap: 16rem;
								position: relative;
								
								.lnb-name {
									display: block;
									@include font((outline: body, size: small-1x)...);
									color: color(normal, 030);
									
									&:not(:hover) {
										text-decoration: none;
									}
								}
							}
						}

						&.current {
							.gnb-name {
								color: color(accent, 010);
							}
						}
					}
				}
			}

			.section-util {
				flex-shrink: 0;
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				row-gap: 14rem;
				margin-top: 56rem;

				.btn.ir-download {
					width: 122rem;
					height: 40rem;
					border-radius: 20rem;
				}

				.btn.inquiry {
					width: 97rem;
					height: 40rem;
					border-radius: 20rem;
				}
			}
		}
	}

	// laptop
	@include respond-above(large-1x) {
		display: flex;
		justify-content: center;
		width: calc(100% - 28rem);

		.section-func {
			display: none;
		}
		.section-wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.section-func {
				display: none;
			}

			.section-head {
				.section-subject {
					@include hidden;
				}
			}
			
			.section-body {
				.gnb-list {
					display: flex;
					align-items: center;
					column-gap: 48rem;
					
					.gnb-item {
						display: flex;
						justify-content: center;
						align-items: center;
						position: relative;
						width: 96rem;
						height: 80rem;
						text-align: center;
						background-color: transparent;

						&.current,
						&:hover,
						&:focus-within {
							.gnb-name {
								// @include font((weight: semi-bold)...);
								color: color(accent, 010);
							}
						}

						.gnb-name {
							position: relative;
							display: block;
							text-decoration: none;
							@include font((outline: body, size: small-1x, weight: semi-bold, family: openSans)...);
							color: color(normal, 030);
							transition: color 0.25s ease;
						}

						.lnb-list {
							display: flex;
							flex-direction: column;
							row-gap: 16rem;
							align-items: center;
							position: absolute;
							top: calc(100% - 12rem);
							// right: 0;
							// left: 0;
							width: 144rem;
							padding: 16rem;
							background-color: color(normal, 000);
							border-radius: 4rem;
							visibility: hidden;
							opacity: 1;

							
							.lnb-item {
								.lnb-name {
									@include font((outline: body, size: small-1x)...);
									color: color(normal, 020);

									&:not(:hover) {
										text-decoration: none;
									}
								}
							}
						}

						&:hover {
							transition: background-color 0.5s ease;

							.lnb-list {
								visibility: visible;
								opacity: 1;
								background-color: color(normal, 010);
								transition: visibility 0.5s ease, opacity 0.5s ease;

								.lnb-item {
									.lnb-name {
										color: color(normal, 000);
									}
								}
							}
						}
					}

					&:not(:hover) {
						.gnb-item {
							&:not(:focus-within) {
								&.current {
									.gnb-name {
										&::after {
											width: 100%;
										}
									}
								}
							}

							&:focus-within {
								.lnb-list {
									visibility: visible;
									opacity: 1;
									background-color: color(normal, 010);
									transition: visibility 0.5s ease, opacity 0.5s ease;

									.lnb-item {
										.lnb-name {
											color: color(normal, 000);
										}
									}
								}
							}
						}
					}
				}
			}

			.section-util {
				position: absolute;
				top: auto;
				right: 80rem;

				.btn.inquiry {
					display: none;
				}
				
				.btn.ir-download {
					padding: 0 13rem;
					height: 40rem;
					box-sizing: border-box;
					border-radius: 20rem;

					
					.btn-text {
						display: flex;
						
						.wbr {
							display: block;
							
							&:nth-child(1) {
								max-width: 0;
								opacity: 0;
								overflow: hidden;
								transition: max-width 0.25s ease, opacity 0.25s ease;
							}
						}
					}

					&:hover {
						text-decoration: none;
						.btn-text {
							display: flex;
							column-gap: 0.25em;

							.wbr {
								&:nth-child(1) {
									opacity: 1;
									max-width: 80rem;
								}
							}
						}
					}
				}
			}
		}
	}
}
// page-body
.page-body {
	&:not(.page-intro) {
		#local {
			.local-head {
				@include hidden;
			}

			.local-body {
				padding-top: 56rem;
				
				// laptop
				@include respond-above(large-1x) {
					padding-top: 80rem;
				}
			}
		}
	}

	&.page-intro {
		#local {
			.local-head {
				display: flex;
				flex-direction: column;
				position: relative;
				box-sizing: border-box;
				justify-content: center;
				align-items: center;
	
				&::before {
					content: '';
					display: block;
					position: absolute;
					z-index: -1;
					top: 0;
					bottom: 0;
					left: calc(50% - 50vw);
					width: 100vw;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					background-color: black;
				}
	
				&::after {
					content: '';
					display: block;
					position: absolute;
					z-index: -1;
					top: 0;
					bottom: 0;
					left: calc(50% - 50vw);
					width: 100vw;
					background-color: rgba(0,0,0,0.5);
					// background-image: icon(circle-arrow-down, color(normal, 000));
					// background-repeat: no-repeat;
					// background-position: center calc(100% - 24rem);
					// background-size: 32rem 32rem;
	
					// laptop
					// @include respond-above(large-1x) {
					// 	background-position: center calc(100% - 40rem);
					// 	background-size: 40rem 40rem;
					// }
				}
	
				// mobile
				@include respond-below(large-1x) {
					height: 320rem;
				}
	
				// laptop
				@include respond-above(large-1x) {
					height: 480rem;
				}
	
				.local-subject {
					margin: 0 0 8rem 0;
					@include font((outline: body, size: small-4x, weight: bold, family: openSans)...);
					color: color(normal, 000);
					letter-spacing: 0.2em;
	
					// laptop
					@include respond-above(large-1x) {
						margin: 0 0 20rem 0;
						@include font((outline: body, size: small-2x)...);
					}
	
					.local-name {
						display: block;
					}
				}
				.local-subtitle {
					margin: 0;
					@include font((outline: head, size: large-2x, family: openSans)...);
					text-align: center;
					color: color(normal, 000);
	
					// laptop
					@include respond-above(large-1x) {
						@include font((outline: head, size: large-7x)...);
					}
	
					.wbr {
						&:nth-child(1) {
							font-weight: 300;
						}
						&:nth-child(2) {
							font-weight: 600;
						}
					}
				}
				.local-summary {
					margin: 4rem 0 0 0;
					text-align: center;
					@include font((outline: body, size: small-2x)...);
					color: color(normal, 000);
	
					.wbr {
						display: block;
					}
								
					// laptop
					@include respond-above(large-1x) {
						margin: 8rem 0 0 0;
						@include font((outline: head, size: small-1x)...);
					}
				}
			}
		}
	}
	#local {
		.local-body {
			position: relative;
			
			#content {
				.content-navi ~ .content-body {
					padding-top: 48rem;

					// laptop
					@include respond-above(large-1x) {
						padding-top: 56rem;
					}
				}

				.content-body {
					padding-top: 0;
				}
			}
		}
	}
}

// content-navi
.content-navi {
	position: absolute;
	z-index: 3;
	top: 0;
	left: calc(50% - 50vw);
	width: 100vw;
	padding: 0 20rem;
	box-sizing: border-box;
	background-color: color(normal, 000);

	@at-root html.snap-content-navi & {
		position: fixed;
		top: 56rem;

		// laptop
		@include respond-above(large-1x) {
			top: 80rem;
		}
	}

	// laptop
	@include respond-above(large-1x) {
		padding: 0 80rem;
	}

	&::before {
		content: '';
		display: block;
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		height: 1rem;
		background-color: color(base, 010);
	}

	.section-head {
		.section-subject {
			@include hidden;
		}
	}
	.section-body {
		// laptop
		@include respond-above(large-1x) {
			justify-content:center;
		}
  
		.navi-list {			
			// laptop
			@include respond-above(large-1x) {
				justify-content:center;
			}
			.navi-item {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-shrink: 0;
				position: relative;
				padding: 0 8rem;
				height: 48rem;

				// laptop
				@include respond-above(large-1x) {
					padding: 0 16rem;
					height: 56rem;
				}

				.navi-text {
					display: block;
					text-decoration: none;
					@include font((outline: body, size: small-2x, weight: semi-bold, family: openSans)...);
					color: color(normal, 030);

					// laptop
					@include respond-above(large-1x) {
						@include font((outline: body, size: small-1x)...);
					}

				}

				&.current {
					&::before {
						content: '';
						display: block;
						position: absolute;
						bottom: 0;
						width: 100%;
						height: 2rem;
						background-color: color(normal, 100);
						border-radius: 2rem;
					}

					.navi-text {
						color: color(normal, 010);
						// @include font((weight: semi-bold)...);
					}
				}
			}
		}
	}
}

// content-path
.content-path {
	// breadcrumb
	.section.breadcrumb {
		margin-top: 16rem;
		
		>.section-wrap {
			.section-head {
				@include hidden;
			}

			>.section-body {
				.data-display {
					.data-list {
						display: flex;

						.data-item {
							display: flex;
							align-items: center;

							+.data-item {
								&::before {
									content: '';
									display: block;
									margin: 0 4rem;
									width: 20rem;
									height: 20rem;
									background: icon(small-chevron-right, color(base, 020)) no-repeat center/cover;
								}
							}

							.data-wrap {
								.data-head {
									.data-name {
										display: flex;
										align-items: center;
										@include font((outline: body, size: small-2x, weight: light, family: openSans)...);
										color: color(normal, 030);

										&:not(:hover) {
											text-decoration: none;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

// section-module-a
.section.module-a {
	.section-wrap {
		.section-head {
			margin-bottom: 48rem;

			// laptop
			@include respond-above(large-1x) {
				margin-bottom: 80rem;
			}

			.section-subject {
				display: flex;
				align-items: baseline;
				column-gap: 4rem;
				margin: 0;
				@include font((outline: head, size: small, weight: semi-bold)...);
				color: color(normal, 100);

				// laptop
				@include respond-above(large-1x) {
					@include font((outline: head, size: large-3x)...);
				}

				&::after {
					content: '';
					display: block;
					width: 4rem;
					height: 4rem;
					border-radius: 50%;
					background-color: color(accent, 010);
	
					// laptop
					@include respond-above(large-1x) {
						width: 6rem;
