						height: 6rem;
					}
				}

				.subsection-name {
					display: block;
				}
			}

			.subsection-summary {
				margin: 0;
				@include font((outline: body, size: small, weight: extra-light)...);

				// laptop
				@include respond-above(large-1x) {
					@include font((outline: head, size: large)...);
				}

				.wbr {
					display: block;
					.em {
						font-weight: 600;
					}
				}
			}


		}
	}

	&.style-a {
		.subsection-wrap {
			.subsection-head {
				display: flex;
				flex-direction: column;
				row-gap: 8rem;

