// subsection-module-b
.subsection.module-b {
	position: relative;

	&::before {
		content: '';
		display: block;
		position: absolute;
		bottom: 0;
		width: 100vw;
		top: 0;
		right: 0;
		left: calc(50% - 50vw);
		bottom: 0;
		z-index: -1;
		background-color: color(base, 005);
	}

	.subsection-wrap {
		position: relative;

		.subsection-head {
			display: flex;
			justify-content: center;
			margin-bottom: 32rem;

			// laptop
			@include respond-above(large-1x) {
				margin-bottom: 48rem;
			}

			.subsection-subject {
				margin: 0;
				@include font((outline: head, size: small-1x, weight: light, family: openSans)...);
				color: color(normal, 010);
				letter-spacing: 3rem;

				// laptop
				@include respond-above(large-1x) {
					@include font((outline: head, size: medium)...);
				}

				.subsection-name {
					display: block;
				}
			}
		}
	}
}

// page-foot
.page-foot {
	position: relative;
	background-color: color(normal, 010);
	padding-top: 56rem;

	&::before {
		content: '';
		display: block;
		position: absolute;
		left: calc(50% - 50vw);
		width: 100vw;
		top: 0;
		bottom: 0;
		z-index: -1;
		background-color: color(normal, 010);
	}

	// laptop
	@include respond-above(large-1x) {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-top: 78rem;
	}

	// widget-toolbar
	.section.widget-toolbar {
		position: fixed;
		z-index: 10;
		bottom: 0;
		right: 0;
		transform: translateY(100%);
		transition: transform 0.25s ease;

		@at-root html.active-scroll-start & {
			transform: translateY(0);
		}

		.section-wrap {
			.section-head {
				@include hidden;
			}

			.section-body {
				.navi-list {
					.navi-item.goto-top {
						.navi-text {
							display: flex;
							justify-content: center;
							align-items: center;
							width: 56rem;
							height: 56rem;
							background-color: color(accent, 010);
							border-top-left-radius: 8rem;
							transition: background-color 0.25s ease;
							
							svg {
								display: block;
								width: 40rem;
								height: 40rem;
								fill: color(normal, 000);
								transform: rotate(0);
								transition: fill 0.25s ease, transform 0.25s ease;
							}

							// laptop
							@include respond-above(large-1x) {
								width: 72rem;
								height: 72rem;

								svg {
									width: 48rem;
									height: 48rem;
								}
							}
						}
					}
				}
			}
		}
	}
	
	.section.reject-email {		
		.section-wrap {
			.section-head {
				@include hidden;
				.section-name {
					margin: 0;
					display: block;
				}
			}
			.section-body {
				.para {
					display: block;
					@include font((outline: body, size: small-1x)...);
					color: color(normal, 000);

					&:not(:hover) {
						text-decoration: none;
					}
				}
			}
		}

		// mobile
		@include respond-below(large-1x) {
			margin-top: 56rem;
		}

		// laptop
		@include respond-above(large-1x) {
			position: absolute;
			right: 0;
			width: calc(183rem + 260rem + 80rem);
		}
	}

	.section.company-info {
		// mobile
		@include respond-below(large-1x) {
			margin: 24rem 0 56rem 0;
		}

		.section-wrap {
			.section-head {
				@include hidden;
			}
			>.section-body {
				>.data-display {
					>.data-list {
						display: flex;
						flex-wrap: wrap;
						column-gap: 20rem;						


						>.data-item {

							.data-wrap {
								display: flex;
								flex-direction: column;
								row-gap: 8rem;
								.data-head {
									@include font((outline: body, size: small-2x,weight: semi-bold)...);
									color: color(normal, 000);
									.data-name {
										display: block;
									}
								}
								.data-body {
									@include font((outline: body, size: small-2x,weight: extra-light)...);
									color: color(normal, 040);

									>.data-list {
										display: flex;
										flex-direction: column;
										row-gap: 8rem;

										.data-item {
											display: flex;
											column-gap: 0.25em;
											
											abbr {
												font-weight: 600;
												color: color(normal, 000);
												text-decoration: none;
											}
										}
									}
								}
							}

							&.business-number {
								.data-wrap {
									flex-direction: row;
									column-gap: 8rem;
								}
							}

							&.ceo {
								.data-wrap {
									flex-direction: row;
									column-gap: 8rem;
								}
							}
							
							&.contact {
								flex: 1 1 100%;
							}

							&.address {
								flex: 1 1 100%;
							}
						}

						
						// mobile
						@include respond-below(large-1x) {
							row-gap: 12rem;
						}


						// laptop
						@include respond-above(large-1x) {
							width: 260rem;
							position: relative;
							row-gap: 36rem;

							.data-item.contact {
								position: absolute;
								top: 0;
								left: -80rem;
								transform: translate(-100%, 0);
							}
						}
					}
				}

				// laptop
				@include respond-above(large-1x) {
					position: absolute;
					top: calc(80rem + 24rem + 34rem);
					right: 0;
				}
			}
		}
	}

	// copyright
	.copyright {
		// laptop
		@include respond-above(large-1x) {
			margin-top: 80rem;
		}

		flex: 1 1 100%;
		margin: 0;
		padding: 12rem 0;
		position: relative;
		text-align: center;
		@include font((outline: body, size: small-3x)...);
		color: color(normal, 000, 0.2);
		border-top: 1rem solid rgba(255, 255, 255, 0.1);
	
		// laptop
		@include respond-above(large-1x) {
			padding: 24rem 0;
			@include font((outline: body, size: small-2x)...);
		}
	}
}

[data-bui-animation="type-1"] {
	>.subsection-wrap,
	>.section-wrap {
		opacity: 0;
		transition: transform 0.75s ease, opacity 0.75s ease;
		transform: translateY(160rem);
	}

	&.active-animation {
		>.subsection-wrap,
		>.section-wrap {
			opacity: 1;
			transform: translateY(0);
		}
	}
}